body,
html,
#root {
  height: 100%;
}

html {
  font-size: 18px;
}

h1 {
  padding: .90rem 0;
  background-color: #57c1e1;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  font-size: 2.22rem;
  color: #fff;
  

}

h1:hover{
  border-color: black 2px;
    background: #0cd7e68e;
    color: white;
    -webkit-box-reflect: below 1px linear-gradient(transparent, #0005);
    box-shadow: 0 0 5px #0cd7e68e, 0 0 25px #0cd7e68e, 0 0 50px #0cd7e68e,
      0 0 200px #0cd7e68e;
}

h1 .highlight {
  color: #fa05b5;
}

h2 {
  font-family: 'Poppins', sans-serif;
  font-size: 1.55rem;
}

.App {
  height: 100%;
  padding: 0 17% 10% 17%;
  background-image: url('./back_pic_jamw_leo.JPG');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
  color: #fff;
}

.App-playlist {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

@media only screen and (max-width: 1020px) {
  .App-playlist {
    align-items: center;
    flex-direction: column;
  }
}